	import {login} from "../../api/user.js"
	import {mapState,mapMutations} from 'vuex'
	import navT from "@/components/nav/nav.vue"

export default {
    components: {navT},
	data(){
		return{
			account:"",
			password:""
		
		}
	},
	methods:{
		...mapMutations(['changToken']),
		toRegister(){
			//页面跳转
			uni.redirectTo({
				url: '../register/register',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		show(val){
			uni.showModal({
				content:val
			})
			
		},
		testPhone(){
			var regex = /^[1][3,4,5,7,8][0-9]{9}$/;
			if(!regex.test(this.account)){
				this.show("请输入正确的手机号")
				return false;
			}else{
				
				return true;
			}
		},
		testPassword(){
			if(this.password.length>=6){
				return true;
			}else{
				this.show("密码格式有问题");
				return false;
				
			}
		},
		login(){
			if(this.testPhone()&&this.testPassword()){
				var data={
					account:this.account,
					password:this.password
				}
					login(data).then((data)=>{
						if(data.data.msg="登录成功"){
							console.log(data.data.data.token)
							this.changToken(data.data.data.token)
							uni.navigateBack({
								delta:1
							})
						}
						
						
					})
			}else{
				console.log(0)
			}
		},
		qq(){
			console.log(this.token)
			var url = "/api/menu/user"
			var data={
				
			}
			Get.urlRequset(url,data).then((data)=>{
				console.log(data)
			})
			
		}
	},
	computed:{
		btn(){
			if(this.accoun!=''&&this.password!=''){
				return true
			}else{
				return false
			}
		},
		...mapState(['token'])
	}
}
